import React, { Component } from 'react'; // https://reactjs.org/docs/installation.html
// import logo from './logo.svg';
import { Button, Icon } from 'react-materialize'
// import './App.css';
import NavBar from './NavBar.js';
import Content from './Content.js';
import Blur from 'react-blur'; // https://github.com/javierbyte/react-blur
import domtoimage from 'dom-to-image'; // https://github.com/tsayen/dom-to-image

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imgSrc: 'jason.jpg'
        };
        // this.handleMouseOver = this.handleMouseOver.bind(this);
        // this.handleMouseOut = this.handleMouseOut.bind(this);
    }
    render() {
        return (
            <div className="App" id='app'>
                <NavBar />
                <img id='origimg' width='500'
                    src={this.state.imgSrc} className="App-logo" alt="logo"
                    style={{
                        display: 'none',
                        border: '1px solid gray'
                    }}
                />
                <Content />
            </div>
        );
    }
    componentDidMount() {
        // to ensure that the code is run **after** the painting of the newly rendered object

        var node = document.getElementById('root');

        domtoimage.toJpeg(node, {
            quality: 0.2,
            bgcolor: "white"
            /*style: {filter: 'blur(15px)'}*/
            // better to apply the blur AFTER the image is compressed
        }).then(function (dataUrl) {
            // var bgimgS = document.getElementById('bgImg');
            // bgimgS.style.backgroundImage = "url(" + dataUrl + ")";
            // domImg = dataUrl
            this.setState({ imgSrc: dataUrl })
            window.blurNAV()
            var img = document.getElementById('origimg');
            // img.id = 'origimg'
        }.bind(this)).catch(function (error) {
            console.error('oops, something went wrong!', error);
        });

    }
    componentDidUpdate() {
    }
}


export default App;


