import React, { Component } from 'react';
import { Button, Icon, SideNav, SideNavItem} from 'react-materialize'



class NavBar extends Component {
    render() {

        return (
            <div>
                <SideNav id="slide-out"  className="side-nav fixed z-depth-2"
                    trigger={<a/>}
                    // options={{ closeOnClick: true }}
                >
                    <SideNavItem userView
                        user={{
                            background: 'orch_small.jpg',
                            image: '1.jpg',
                            name: 'Jason Anchia',
                            email: 'jasonanchia@hotmail.com'
                        }}
                    />
                    <SideNavItem href='tg://resolve?domain=JasonAnx' icon='message'>Telegram</SideNavItem>
                    <SideNavItem href='./cv.pdf' target="_blank" icon='cloud_download'>Download CV</SideNavItem>
                    <SideNavItem divider />
                    <SideNavItem subheader>Subheader</SideNavItem>
                    <SideNavItem waves href='#!third'>Third Link With Waves</SideNavItem>
                </SideNav>
                {/*
                <!-- ******************************************************************************************
                    NAV BAR
                ********************************************************************************************* -->
                */
                }
                <div id="canvasHolder" style={{
                    maxHeight: 64,
                    maxWidth: 100 + '%',
                    width: 100 + '%',
                    position: 'fixed',
                    overflow: 'hidden',
                    zIndex: 3,
                    // border: 1 + 'px solid black'
                }} >
                    <canvas id="canvas" />
                </div>
                <div className="navbar-fixed">
                    <nav className="z-depth-2 white darken-4">
                        <div className="nav-wrapper content">
                            <a href="#" className="brand-logo black-text center">Jason Anchia</a>
                            <a href="#" data-activates="slide-out" className="button-collapse black-text">
                                <i className="material-icons">menu</i>
                            </a>
                            {/*
                            <!-- Desktop -->
                            */
                            }
                            <ul className="left hide-on-med-and-down">
                                <li>
                                    <a href="#" data-activates="slide-out" className="anxmenu black-text">
                                        <i className="material-icons">menu</i>
                                    </a>
                                </li>
                            </ul>

                        </div>
                    </nav>
                </div>
            </div>
        )
    }
}
export default NavBar;



